import { Box, Grid, Stack, Typography } from '@mui/material';
import React, { useEffect, useContext, useState } from 'react';
import { useParams } from 'react-router-dom';
import { DataContext } from '../Contexts/ContextsAPI';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import VisibilityIcon from '@mui/icons-material/Visibility';
import { CheckCircle } from '@mui/icons-material';
import { demoProfilePicture } from '../utils/constansts';
import Videos from './Videos';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import { Helmet } from 'react-helmet';

const ChannelDetails = () => {
  const {setVideos, getData_channel,getData_Global,customNum} = useContext(DataContext);
  const [channel, setChannel]= useState(null)
  const { id }= useParams()
  let [video, setVideo] = useState(true)
  useEffect(()=>{
    getData_channel(`/channels?part=snippet&id=${id}`)
    .then( (data) => setChannel(data?.items[0] ))
    
    getData_Global(`/search?channelId=${id}&part=snippet
    &order=date`).then( (data) => setVideos(data?.items))
  },[id])

  return <>
    <Helmet>
      <meta charSet="utf-8" />
      <title>{channel?.snippet?.title}</title>
    </Helmet>
    <Box>
      <Stack sx={{ display: "flex", flexDirection: "column", 
      alignItems: "center", justifyContent: "center", 
      backgroundImage: `url("${channel?.brandingSettings?.image?.bannerExternalUrl}")`,
       backgroundRepeat:"no-repeat", backgroundPosition:"0", backgroundSize:"cover"
       , height:"250px" }}>
      </Stack>
          <Stack direction="row" alignItems="center" m={2}>
          <img style={{  borderRadius: "50px", height: "100px", width: "100px"}} src={channel?.snippet?.thumbnails?.high?.url || demoProfilePicture} alt={channel?.snippet?.title}/>
        <Stack ml={2}>
              <Typography variant='subtitle1' color="gray" >
              {channel?.snippet?.title}
            {channel !== null && channel.statistics.subscriberCount.length >= 6 ? <CheckCircle sx={{ fontSize: "12px", marginLeft: "5px" }} /> : ""}
            </Typography>
              <Typography variant='subtitle2' color="gray" >
              {channel?.snippet?.customUrl}
            </Typography>
            {channel?.statistics?.subscriberCount && <Typography  variant="subtitle2" color="gray">
            {customNum(channel.statistics.subscriberCount)} Subscribers
            </Typography>}
              </Stack>
          </Stack>
          <Stack px={2}>
        <Box
          sx={{
            display: 'flex',
            '& > *': {
              m: 1,
            }, display: "flex",flexDirection:"column",  alignItems: "center"
          }}
        >
          <ButtonGroup
            orientation="horizontal"
            aria-label="horizontal outlined button group"
            variant="text"
          >
            <Button key="videos" sx={{color:"red"}} onClick={()=> setVideo(true)}>Videos</Button>
            <Button sx={{ color: "red" }} onClick={()=> setVideo(false)} key="info">Info</Button>
          </ButtonGroup>
        </Box>
            {!video? <Grid container >
              <Grid item xs={12} md={9}>
            <Typography variant="subtitle2" color="gray" pr={4}>
              {channel?.snippet?.description}
            </Typography>
                </Grid>
              <Grid item xs={12} md={3} >
                <Typography variant='body1' color="gary" fontWeight="bold">
                  Statistics
                </Typography>
            <Box display={{ xs: "flex", md: "block" }} p={{xs:0,md:1}}  alignItems="center" justifyContent="space-around">
              <Typography variant='subtitle2' color="gray" display="flex" alignItems="center">
                <span style={{ fontWeight: "bold" }}>Subscribers</span>
                <span style={{ marginLeft: "5px" }}> { channel !== null && customNum(channel.statistics.subscriberCount)} </span>
                <AddCircleIcon mr={2} />
              </Typography>
              <Typography variant='subtitle2' color="gray" display="flex" alignItems="center">
                <span style={{ fontWeight: "bold" }}>Videos</span>
                <span style={{ marginLeft: "5px" }}> {channel !== null && customNum(channel.statistics.videoCount)} </span>
                <PlayCircleOutlineIcon mr={2} />
              </Typography>
              <Typography variant='subtitle2' color="gray" display="flex" alignItems="center">
                <span style={{ fontWeight: "bold" }}>Views</span>
                <span style={{ marginLeft: "5px" }}> {channel !== null && customNum(channel.statistics.viewCount) }</span>
                <VisibilityIcon mr={2} />
              </Typography>
                </Box>
              </Grid>
              </Grid>:""}
            {video && <Videos   />}
        
          </Stack>
    </Box>
  </>
}

export default ChannelDetails