import { CheckBox, CheckCircle } from '@mui/icons-material'
import { Card, CardContent, CardMedia, Typography } from '@mui/material'
import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import { demoChannelTitle, demoThumbnailUrl, demoVideoTitle, demoVideoUrl } from '../utils/constansts'

const VideoCard = ({video:{id:{videoId},snippet}}) => {
  return <>
    <Card sx={{ borderRadius: { xs: 0, md: "10px" } }} style={{ backgroundColor: "transparent" }}>
    <Link to={videoId? `/video/${videoId}`: demoVideoUrl} >
          <CardMedia
          image={snippet?.thumbnails?.high?.url || demoThumbnailUrl}
          alt={snippet?.title}
          sx={{width:"100%" , height:"145px"}}
          />
    </Link>
          <CardContent sx={{height:"105px", backgroundColor:"#1e1e1e"}}>
          <Link to={videoId ? `/video/${videoId}` : demoVideoUrl}>
            <Typography variant='subtitle1' color="white">
              {snippet?.title.slice(0,50)+".."|| demoVideoTitle.slice(0,60)}
            </Typography>
        </Link>
        {/* channel link      */}
        <Link to={ `/channel/${snippet.channelId}` } p={{ xs: 0, md: 2 }} >
          <Typography variant='subtitle2' sx={{ color: "gray", display: "flex", alignItems: "center" }}>
              {snippet?.channelTitle.slice(0,60) || demoChannelTitle.slice(0,60)}
            </Typography>
        </Link>
        </CardContent>
        </Card>
  </>
}

export default VideoCard