import { Box, Typography } from '@mui/material'
import React, { useContext, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { DataContext } from '../Contexts/ContextsAPI'
import Videos from './Videos'
import LoadingScreen from 'react-loading-screen';
import { Helmet } from 'react-helmet'

const SearchFeed = () => {
  const { setVideos, getData_Global, loading }= useContext(DataContext)

  const { searchItem }= useParams()
  useEffect(()=>{
    getData_Global(`/search?part=snippet&q=${searchItem}`).then((data)=> setVideos(data.items))
  }, [searchItem])
  if (loading) return <LoadingScreen
    loading={true}
    bgColor='black'
    spinnerColor='red'
  >   </LoadingScreen>
  return <>
    <Helmet>
      <meta charSet="utf-8" />
      <title>Search Feed</title>
    </Helmet>
    <Box>
      <Box p={{ xs: 0, md: 2 }} >
        <Typography variant='h4' sx={{ fontWeight: "bold" }}>
          Results for:  <span style={{ color: "red" }}>{searchItem} </span>Videos
        </Typography>
        <Videos />
      </Box>
    </Box>
  </>
}

export default SearchFeed