import { CheckCircle } from '@mui/icons-material';
import { Box, Button, ButtonGroup, Grid, Stack, Typography , Drawer  } from '@mui/material';
import React, { useContext, useEffect, useState } from 'react';
import ReactPlayer from 'react-player';
import { Link, useParams } from 'react-router-dom';
import { DataContext } from '../Contexts/ContextsAPI';
import { demoProfilePicture } from '../utils/constansts';
import ThumbUpOutlinedIcon from '@mui/icons-material/ThumbUpOutlined';
import ThumbDownAltOutlinedIcon from '@mui/icons-material/ThumbDownAltOutlined';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import ThumbDownAltIcon from '@mui/icons-material/ThumbDownAlt';
import Linkify from 'linkify-react';
import LoadingScreen from 'react-loading-screen';
import ShareIcon from '@mui/icons-material/Share';
import { FacebookIcon, FacebookShareButton,
   LinkedinIcon, LinkedinShareButton, TwitterIcon,
    TwitterShareButton, WhatsappIcon, WhatsappShareButton } from 'react-share';
import { Helmet } from 'react-helmet';
import VideoCard from './VideoCard';


const VideoDetails = () => {
  const {id}= useParams()
  const [videoDetail, setVideoDetail] = useState(null)
  const [channel, setChannel] = useState(null)
  const {getData_videos, getData_channel, getData_Global, setVideos, videos, customNum,loading}= useContext(DataContext)
  const [like, setLike] = useState(false)
  const [dislike, setDislike] = useState(false)
  const [len, setLen] = useState([0,140])
  const [readingState, setReadingState] = useState(false)
  const [likes,setLikes] = useState("0")
  const [shareField, setShareField]= useState(false)
  const reading= ()=>{
      if (readingState === false) {
        setLen([0,])
        setReadingState(true)
      } else {
        setLen([0, 140])
        setReadingState(false)
      }
  }
  const reaction = (react, str)=>{
    if(react===true){
      if (str === "dislike") {
        setDislike(false)
      }
      if(str=== "like")
      setLike(false)
      setLikes(customNum(parseInt(videoDetail.statistics.likeCount).toString()))

    } else if(react===false){
      if (str === "dislike") {
        setDislike(true)
      } if (str === "like"){
        setLike(true)
        setLikes(customNum((parseInt(videoDetail.statistics.likeCount) + 1).toString()))
        }    } 
    if(like===false && dislike===true){
      if(str=== "like"){
        setDislike(false)
        setLikes(customNum((parseInt(videoDetail.statistics.likeCount) + 1).toString()))
      }
    }else if(like===true && dislike===false){
      if(str=== "dislike"){
        setLike(false)
        setLikes(customNum(parseInt(videoDetail.statistics.likeCount).toString()))
      }
    }
    }
  const options = {
    className:"link",
  target:"_blank"}

  useEffect(()=>{
    getData_Global(`/videos?part=snippet,statistics&id=${id}`)
    .then((data) => setVideoDetail(data.items[0]))

    getData_videos(`/search?part=snippet&relatedToVideoId=${id}&type=video`)
      .then((data) => setVideos(data.items))
    
  },[id])
  useEffect(()=>{
    getData_channel(`/channels?part=snippet&id=${videoDetail?.snippet?.channelId}`)
      .then((data) => setChannel(data.items[0])).then(() => setLikes(customNum(videoDetail.statistics.likeCount)))
    },[videoDetail])
  

  if (loading || videos== null) return <LoadingScreen
    loading={true}
    bgColor='black'
    spinnerColor='red'
  >   </LoadingScreen>
  
return <>
  <Helmet>
    <meta charSet="utf-8" />
    <title>{videoDetail?.snippet?.title}</title>
  </Helmet>
      <Grid container spacing={1} p={{ xs: 0, md: 2 }} >
        <Grid item md={8}  xs={12}  >
          <Stack>
      <ReactPlayer className="react-player" url={`https://www.youtube.com/watch?v=${id}`}/>
            <Typography variant="h6" p={{xs:2,md:0}}>
              {videoDetail?.snippet?.title}
            </Typography>
    </Stack>
          <Stack>
            <Box p={2}>
          <Stack direction={{ xs: "column", md: "row " }} alignItems={{ xs:"start", md: "center" }} justifyContent="space-between">
                <Stack direction="row"  alignItems="center" justifyContent= "start" >
                  <Link to={`/channel/${videoDetail?.snippet?.channelId}`} >
                    <img style={{ borderRadius: "30px", height: "60px", width: "60px" }} src={channel?.snippet?.thumbnails?.high?.url || demoProfilePicture} alt={channel?.snippet?.title} />
                  </Link>
                  <Stack ml={1} >
                    <Link to={`/channel/${videoDetail?.snippet?.channelId}`} >
                      <Typography variant='subtitle2' color="gray" >
                        {channel?.snippet?.title}
                        {channel !== null && channel.statistics?.subscriberCount?.length >= 6 ? <CheckCircle sx={{ fontSize: "12px", marginLeft: "5px" }} /> : ""}
                      </Typography>
                    </Link>
                    {channel?.statistics?.subscriberCount && <Typography variant="subtitle2" color="gray">
                      {customNum(channel.statistics.subscriberCount)} Subscribers
                    </Typography>}
                  </Stack>
                </Stack>
            <Stack mt={3} justifyContent="space-between" width={{ xs:"100%", md:"auto"}} direction="row" > 
                  <ButtonGroup variant="contained"  className='btn-par' aria-label="outlined  button group">
                    <Button className='btn' onClick={() => reaction(like, "like")} >
                      <span style={{marginTop:"4px"}}> {likes} </span>
                      {like === true ? <ThumbUpIcon /> : <ThumbUpOutlinedIcon />}
                    </Button>
                    <Button className='btn' onClick={() => reaction(dislike, "dislike")}>
                      {dislike === true ? <ThumbDownAltIcon /> : <ThumbDownAltOutlinedIcon />}
                    </Button>
                  </ButtonGroup>
                  <Button variant="contained" onClick={() => setShareField(true)} className='btn' >
                  Sahre <ShareIcon/> 
                </Button>
                </Stack>
              </Stack>
            </Box>
          </Stack>
          <Stack>
            <Stack sx={{ backgroundColor: " rgba(128, 128, 128,.3)", p: 2, borderRadius: { xs: 0, md: "10px" } }}>
              <Typography variant='subtitle1'>
                <span>Published at: </span>{videoDetail?.snippet?.publishedAt.slice(0, 9)}
              </Typography>
              <Typography variant='subtitle2'>
                {videoDetail?.statistics.viewCount} <span> Views</span>
              </Typography>
              <Typography variant='body1'>
                Descripton:
              </Typography>
              <Typography variant="body2"   >
                <Linkify options={options}>
                  {videoDetail?.snippet?.description.slice(...len)}
                </Linkify>
                <button onClick={() => reading()} type="button" className='readMore' style={{ border: "unset", fonnSize: "18px", backgroundColor: "transparent", color: "white" }} >
                  {140 < videoDetail?.snippet?.description.length  ? !readingState === true ? "..Read More" : "Read Less" : "" }
                </button>
              </Typography>
            </Stack>
          </Stack>
          <Stack sx={{
            position: { xs: "static", md:"sticky"}, top:{md:"100px" ,xs:"unset"}, left:0, 
        justifyContent:"center", alignItems:"center"}}>
          <Typography variant='h4' color="gray">
              We Will add comments soon...
          </Typography>
        </Stack>
      </Grid>
        <Grid item xs={12} md={4}>
          {videos.map((card)=> <> {card.id.videoId && <Grid item key={card.id.videoId} mb={{xs:0, md:1}}  >
          <VideoCard video={card} />
        </Grid> } </>)}
        </Grid>
    </Grid>
      <Drawer open={shareField} onClose={()=>setShareField(false)}  anchor="bottom">
        <Typography variant='h6' fontWeight="bold" color="white" sx={{ backgroundColor: "rgb(0, 0, 0) " ,p:1 }}>
            Share Video
          </Typography>
        <Stack direction="row" justifyContent="center" spacing={1} sx={{ backgroundColor: "rgb(0, 0, 0) " ,py:2}} >
          <FacebookShareButton
            url={`https://www.youtube.com/watch?v=${id}`}
            quote={'share video from youtube'}
            >
            <FacebookIcon round={true} size="40px"/>
          </FacebookShareButton>
          <WhatsappShareButton
            url={`https://www.youtube.com/watch?v=${id}`}
            quote={'share video from youtube'}
            >
            <WhatsappIcon round={true} size="40px"/>
          </WhatsappShareButton>
          <LinkedinShareButton
            url={`https://www.youtube.com/watch?v=${id}`}
            quote={'share video from youtube'}
            >
            <LinkedinIcon round={true} size="40px"/>
          </LinkedinShareButton>
          <TwitterShareButton
            url={`https://www.youtube.com/watch?v=${id}`}
            quote={'share video from youtube'}
            >
            <TwitterIcon round={true} size="40px"/>
          </TwitterShareButton>
        </Stack>
      </Drawer>
  </>
}

export default VideoDetails