import { Grid } from '@mui/material'
import React, { useContext } from 'react'
import { DataContext } from '../Contexts/ContextsAPI'
import {ChannelCard, VideoCard} from "./"
import LoadingScreen from 'react-loading-screen';

const Videos = () => {
  const {videos,loading}= useContext(DataContext)
   if (loading || videos== null) return <LoadingScreen
    loading={true}
    bgColor='black'
    spinnerColor='red'
  >   </LoadingScreen>
  return (
    <Grid container spacing={{xs:0,md:1}} mt={2} px={{xs:0, md:2}}  >
    {videos.map((card)=><>
      {card.id.channelId && <Grid key={card.id.channelId} sx={{
        height:"250px"}} item xs={12} sm={6} md={3} >
         <ChannelCard channel={card}/>
      </Grid>}
      {card.id.videoId && <Grid item key={card.id.videoId} xs={12} sm={6} md={3}  >
         <VideoCard video={card}/>
      </Grid>}
    </>
    )}
    </Grid>
  )
}

export default Videos

