import axios from 'axios';
import React, { createContext, useState } from 'react';

export const DataContext = createContext();


const ContextsAPI = ({ children }) => {
  const [selectedCategory, setSelectedCategory] = useState("New")
  const [videos, setVideos] = useState(null)
  const baseUrl = "https://youtube-v31.p.rapidapi.com"
  const [loading, setLoading] = useState(false)
  const customNum = (subCount) => {
    if (subCount.length === 4) {
      return subCount.slice(0, 1) + "." + subCount.slice(1, 2) + "K"
    }
    else if (subCount.length === 5) {
      return subCount.slice(0, 2) + "." + subCount.slice(2, 3) + "K"
    }
    else if (subCount.length === 6) {
      return subCount.slice(0, 3) + "." + subCount.slice(3, 4) + "K"
    }
    else if (subCount.length === 7) {
      return subCount.slice(0, 1) + "." + subCount.slice(1, 2) + "M"
    }
    else if (subCount.length === 8) {
      return subCount.slice(0, 2) + "." + subCount.slice(1, 2) + "M"
    }
    else if (subCount.length === 9) {
      return subCount.slice(0, 3) + "." + subCount.slice(3, 5) + "M"
    }
    else if (subCount.length === 10) {
      return subCount.slice(0, 1) + "." + subCount.slice(1, 2) + "B"
    }
    else if (subCount.length === 11) {
      return subCount.slice(0, 2) + "." + subCount.slice(2, 3) + "B"
    }
    else {
      return subCount
    }
  }
  const options_Global = {
    params: { maxResults: '50' },
    headers: {
      'X-RapidAPI-Key': process.env.REACT_APP_RAPID_API_KEY_1,
      'X-RapidAPI-Host': 'youtube-v31.p.rapidapi.com'
    }
  };
  const options_videos = {
    params: { maxResults: '50' },
    headers: {
      'X-RapidAPI-Key': process.env.REACT_APP_RAPID_API_KEY_2,
      'X-RapidAPI-Host': 'youtube-v31.p.rapidapi.com'
    }
  };
  const options_channel = {
    params: { maxResults: '50' },
    headers: {
      'X-RapidAPI-Key': process.env.REACT_APP_RAPID_API_KEY_3,
      'X-RapidAPI-Host': 'youtube-v31.p.rapidapi.com'
    }
  };

  async function getData_videos(url) {
    setLoading(true)
    const { data } = await axios.get(`${baseUrl}${url}`, options_videos)
    setLoading(false)
    return data
  }
  async function getData_channel(url) {
    setLoading(true)
    const { data } = await axios.get(`${baseUrl}${url}`, options_channel)
    setLoading(false)
    return data
  }
  async function getData_Global(url) {
    setLoading(true)
    const { data } = await axios.get(`${baseUrl}${url}`, options_Global)
    setLoading(false)
    return data
  }
  return <DataContext.Provider value={{
    selectedCategory, setSelectedCategory,
    getData_Global,getData_channel,getData_videos, setVideos, videos, customNum, loading
  }}>
    {children}
  </DataContext.Provider>

}

export default ContextsAPI

