import { Box, Stack, Typography } from '@mui/material'
import React, { useContext, useEffect } from 'react'
import { Helmet } from 'react-helmet'
import { DataContext } from '../Contexts/ContextsAPI'
import {SideBar, Videos} from "./"
const Feed = () => {
  const { selectedCategory, getData_Global, setVideos } = useContext(DataContext)
  useEffect(() => {
    getData_Global(`/search?part=snippet&q=${selectedCategory}`).then((data)=>setVideos(data.items))
  }, [selectedCategory])
  
  return <>
    <Helmet>
      <meta charSet="utf-8" />
      <title>Youtube Clone</title>
    </Helmet>
    <Stack sx={{flexDirection:{xs:"column", md:"row"}}}>
      <Box sx={{height:{xs:"auto" , md:"100vh",position:"sticky",
       borderRight:"#3d3d3d 1px solid", 
        px: { sx: 0, md: 2 }
      }, position: "sticky", top: "72px", bottom: 0, zIndex:9999999
}}
        >
        <SideBar/>
      </Box>
      <Box p={{xs:0, md:2}} >
        <Typography variant='h4' sx={{fontWeight:"bold"}}>
          {selectedCategory} <span style={{color:"red"}}>Videos</span>
        </Typography>
        <Videos/>
      </Box>
    </Stack>
  </>
}

export default Feed