import { Stack } from '@mui/material'
import { useContext } from 'react'
import { DataContext } from '../Contexts/ContextsAPI'
import { categories } from '../utils/constansts'
const SideBar = ()=> {
  const { selectedCategory, setSelectedCategory } = useContext(DataContext)

  return <>
    <Stack sx={{
      flexDirection: { xs: "row", md: "column" },
      overflowY: "auto",
      px: { xs: 0, md: 2 }, height: { xs: "auto", md: "95%" } , backgroundColor:"black"   }}>
      {categories.map((category, index) => <button
        className="category-btn"
        style={{ color: "#eee", background: category.name === selectedCategory && "#FC1503" }}
        key={index}
        onClick={() => setSelectedCategory(category.name)}
        >
        <span style={{ color: category.name === selectedCategory ? "white" : "red", opacity: category.name === selectedCategory ? 1 : .8, marginRight: 10 }}>{category.icon}</span>
        <span>{category.name}</span>
      </button>)}
    </Stack>
  </>
}
    

export default SideBar