import { CheckCircle } from '@mui/icons-material'
import { Box, Card, CardContent, CardMedia, Typography } from '@mui/material'
import React, { useContext } from 'react'

import { Link } from 'react-router-dom'
import { DataContext } from '../Contexts/ContextsAPI'
import { demoChannelUrl, demoProfilePicture } from '../utils/constansts'


const ChannelCard = ({ channel: { id: { channelId }, snippet, statistics }}) => {
  const {customNum} = useContext(DataContext)
  return <>
    <Box >
      <Card sx={{display:"flex", flexDirection:"column",height:"298px" , backgroundColor:"transparent", alignItems:"center", justifyContent:"center"}}>
        <Link to={ channelId?`/channel/${channelId}`: demoChannelUrl}>
        <CardMedia
          image={snippet?.thumbnails?.high?.url || demoProfilePicture}
          alt={snippet?.title}
          sx={{borderRadius:"60px", height:"120px", width:"120px"}}
        />
        </Link>
        <CardContent>
          <Link to={channelId ? `/channel/${channelId}` : demoChannelUrl} >
            <Typography variant='subtitle1' color="gray" >
              {snippet?.title}
              {statistics !== null && statistics?.subscriberCount?.length >= 5 ?<CheckCircle sx={{ fontSize: "12px", marginLeft: "5px" }} />: ""}
            </Typography>
            {statistics?.subscriberCount && <Typography variant="subtitle2" color="gray">
                      {customNum(statistics.subscriberCount)} Subscribers
              </Typography>}
          </Link>
        </CardContent>
      </Card>
    </Box>
  </>
}

export default ChannelCard